#two-factor-qr-code {
  display: flex; /* Enables Flexbox */
  justify-content: left; /* Centers horizontally */
  align-items: center; /* Centers vertically */
  width: 100%;
  min-height: 100%;
}

#qr-code-container {
  margin-bottom: 20px;
  position: relative;
  text-align: center;
  //right: 0;
}

#two-factor-totp-authcode {
  width: 100%;
}
tr.rsssl_verify_email {
  display: none;
}
.error {
  color: red;
  margin-top: -5px;
}

span.rsssl-backup-codes {
  padding: 5px;
  background: #fbebed;
  border-radius: 8px;
  box-shadow: rgba(0,0,0,0.1) 0 4px 6px -1px;
}

.input {
  margin-bottom: 5px !important;
}

#totp-key {
  cursor: pointer;
  display: flex; /* Enables Flexbox */
  justify-content: center; /* Centers horizontally */
  align-items: center; /* Centers vertically */
}

table.rsssl-table-two-fa {
  padding-bottom: 20px;
}

#rsssl_step_three_onboarding {
  display: none;
}

.rsssl-methods-tag {
  padding: 2px 5px;
  border: 1px solid #000;
  color: #000;
  margin-left: 5px;
  background: dimgrey;
  &.active {
    background: darkgreen;
    color: #fff;
  }
}
.rsssl_datatable {
  background: white;
 // padding: 1em;
  thead {
    border-bottom: black 1px solid;
  }
}
.column-device_name, .column-actions {
  padding-left: 10px !important;
}

.column-registered, .column-last_used {
  padding-top: 13px !important;
}

.passkey-datatable {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top:-10px;
  background-color: #f1f1f1;
  border-bottom: 1px solid #ddd;

  .rsssl-passkey-button {
    background-color: #f6f7f7;
    color: #2271b1;
    border: 1px solid #2271b1;
    padding: 5px 10px;
    text-decoration: none;
    border-radius: 3px;
    &.pending {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      color: #1e1e1e;
      opacity: 1;
      cursor: not-allowed;
    }
    &.failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
    &.success {
      background-color: #b8e6bf;
      border-color: #68de7c;
      color: #1d2327;
      opacity: 1;
    }
    &.login {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      color: #1d2327;
      opacity: 1;
      cursor: not-allowed;
    }
    &.login-failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
  }
  .passkey-datatable-title {
    font-size: 16px;
    font-weight: 600;
    padding: 10px 0;
  }
}