.passkey-integration {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .passkey-registration-button {
    width: 100%;
    background: #2271b1;
    border-color: #2271b1;
    color: #fff;
    text-decoration: none;
    text-shadow: none;
    display: inline-block;
    font-size: 13px;
    line-height: 2.15384615;
    min-height: 30px;
    margin: 0;
    padding: 0 10px;
    cursor: pointer;
    border-width: 1px;
    border-style: solid;
    -webkit-appearance: none;
    border-radius: 3px;
    white-space: nowrap;
    box-sizing: border-box;
    &.pending {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      color: #1e1e1e;
      opacity: 1;
      cursor: not-allowed;
    }
    &.failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
    &.success {
      background-color: #b8e6bf;
      border-color: #68de7c;
      color: #1d2327;
      opacity: 1;
    }
    &.login {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      color: #1d2327;
      opacity: 1;
      cursor: not-allowed;
    }
    &.login-failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
  }
}

#rsssl-passkey-error {
  color: #dc3232; // Standard WordPress error color
  background-color: #fef7f7;
  border: 1px solid #dc3232;
  padding: 10px;
  margin-top: 10px;
  width: 100%; // Or a suitable max-width
  min-width: 280px; // Ensure a minimum width for the error message
  box-sizing: border-box; // Ensures padding and border don't increase the specified width
  overflow-wrap: break-word; // Allows long words to break and wrap
  word-wrap: break-word; // older browser compatibility for overflow-wrap
  word-break: normal; // Prevents breaking within words unless absolutely necessary by overflow-wrap
  hyphens: none; // Disable automatic hyphenation
  white-space: normal; // Ensure text wraps normally
  text-align: left; // Align text to the left
  display: none; // Initially hidden, shown by JS
}