
/* Extra css styling for login when passkey is enabled */
#login:has(#passkey-login-button) {
  #wp-submit, #rememberme {
    margin-top: 4px;
  }

  #passkey-login-button {
    &.button-primary {
      margin: 0 auto;
        display: block;
      font-size: 16px;
      padding: 0 16px;
    }
  }
  .or-divider {
    margin-top: 20px;
    margin-bottom: 10px;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: #000;
    position: relative;
    &::before, &::after {
      content: '';
      flex: 1;
      border-bottom: 1px solid #000;
    }
    &::before {
      margin-right: 10px;
    }
    &::after {
      margin-left: 10px;
    }
  }
}

.validation_button_holder {
  display: flex;
  justify-content: space-between;
  align-items: center;
  #rsssl-passkey-button {
    margin-top: 10px;
    padding: 10px 20px;
    width: 100%;
    &.pending {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      opacity: 1;
      color: #1e1e1e;
      cursor: not-allowed;
    }
    &.failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
    &.success {
      background-color: #b8e6bf;
      border-color: #68de7c;
      color: #1d2327;
      opacity: 1;
    }
    &.login {
      background-color: #f5e6ab;
      border-color: #f0c33c;
      color: #1d2327;
      opacity: 1;
      cursor: not-allowed;
    }
    &.login-failed {
      background-color: #facfd2;
      border-color: #f86368;
      color: black;
    }
  }

  .button {
    width: 100%;
  }
}
